@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap');
:root {
    --font-family: 'Roboto', sans-serif;
    --main-color-text: rgb(27, 26, 26);
    --second-color-text: rgba(131, 127, 127, 0.571);
    --backg-card-user: white;
}

.profile {
    width: 250px;
   font-family: var(--font-family);
   background-color: var(--backg-card-user);
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   margin: 0 auto;
   text-align: center;
   padding-top: 25px;
   border-radius: 6px;
   box-shadow: var(--second-color-text);
}


.avatar {
    width: 100px;
    height: 100px;
    border-radius: 100%;
    margin-bottom: 15px;
}
.userName {
    font-size: 18px;
    font-weight: bold;
    color: var(--main-color-text);
}
.descr {
    width: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4px;
    margin-bottom: 30px;
}
.tag, .location {
    color: var(--second-color-text);
    font-size: 14px;
}
.stats {
    position: relative;
    display: flex;
    gap: 10px;
    width: 250px;
    justify-content: space-evenly;
    font-family: var(--font-family);
    background-color: rgba(230, 230, 238, 0.463);
}
.stats::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: rgba(230, 230, 238, 0.589);
}

.stats li {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 15px;
    flex-basis: calc((100% - 20px) / 3);

}



.stats li:not(:last-child)::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 100%;
  width: 1px;
  height: 100%;
  background-color: rgba(230, 230, 238, 0.588);
  transform: translateY(-50%);
}
.label {
    color: var(--second-color-text);
    font-size: 12px;
}
.quantity {
    color: var(--main-color-text);
    font-size: 14px;
    font-weight: bold;
}